<!---->
<template>
<div >


            <p class="fz24 ls"></p>
            <p class="fz12  z9"></p>
             <section class="msd_for_rt">
                 <el-form :model="ruleForm" :rules="rules" ref="ruleForm"  class="demo-ruleForm">
            
            <el-form-item  prop="name">
    <el-input v-model="ruleForm.name" placeholder=""></el-input>
  </el-form-item>
                   
    <el-form-item  prop="password" class="pr">
    
        <el-tooltip class="item" effect="dark"  placement="top">
                <section slot="content">
            <p> 8</p>
                    <p class="ls mt5"> </p>
            </section>
        <i class="dx icon-iconset0143 fz20 z9 df_jh_deert sz"></i>
    </el-tooltip>
    <el-input v-model="ruleForm.password" placeholder="" type="password"></el-input>
  </el-form-item>
                     
                     
                         <el-form-item  prop="password" class="pr">
    
        <el-tooltip class="item" effect="dark"  placement="top">
                <section slot="content">
            <p> 8</p>
                    <p class="ls mt5"> </p>
            </section>
        <i class="dx icon-iconset0143 fz20 z9 df_jh_deert sz"></i>
    </el-tooltip>
    <el-input v-model="ruleForm.password_q" placeholder="" type="password"></el-input>
  </el-form-item>
                     
                     
    <el-form-item class="pr">
        <el-row>
            <el-col :span="8">
         <el-input v-model="ruleForm.xing" placeholder=""></el-input>
            </el-col>
            <el-col :span="16" class="pl20">
     <el-input v-model="ruleForm.ming" placeholder=""></el-input>
    </el-col>
        </el-row>
                           
    </el-form-item>
                     
                     
                     
                       <el-form-item  prop="dizhi">
    <el-input v-model="ruleForm.dizhi" placeholder=""></el-input>
  </el-form-item>
                     
                       <el-form-item  prop="phone">
    <el-input v-model="ruleForm.phone" placeholder="
"></el-input>
  </el-form-item>
                      
                     
                     
              <el-form-item class="mt30">
    <el-button type="primary" @click="submitForm('ruleForm')" class="w100"></el-button>
                    
    </el-form-item>        
                     
    </el-form>
                 
                 <section class="mt20">
<el-row>
    <el-col :span="12" class="fz12 tl sz">
     <span @click="$store.state.id_der=0">
       ?
    </span>
   
    </el-col>
    <el-col :span="12" class="tr fz12 sz">
          <span @click="$store.state.id_der=2 ">
    ?
    </span>

    </el-col>
</el-row>
                     
    </section>
                 
                 
    </section>
            

    
</div>
</template>
<script>
    export default {
        data() {
            return {
                ruleForm: {
                    name: "",
                    password: ""
                },
                rules: {
                    name: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    }, {
                        type: 'email',
                        message: '',
                        trigger: ['blur', 'change']
                    }],
                    password: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    }],
                    password_q: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    }],
                    dizhi: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    }],
                    phone: [{
                        required: true,
                        message: '',
                        trigger: 'blur'
                    }]


                }
            }
        },
        components: {

        },
        methods: {
            submitForm(formName) { //
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        alert('!');
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            }
        },
        mounted() {

        },
    }

</script>



